.hamburger{
    display: none;
    margin-left: 30px;
    cursor: pointer;
    color: white;
}
nav{
    background-color: #000000;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 15vh;
    border-radius: 0 0 20px 20px;
    top: 0;
    position: fixed;
    
    
}
.logoNavBar{
    
    width: 128px;
    height: inherit;
    margin-right: 30px;
    position:absolute;
    top:0;
    left:50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    
}
@media screen and (max-width: 768px) {
    .hamburger{
        display: block;
    }
    nav{
        color: white;
        z-index: 1; 

    }
   nav #show {
        flex-direction: column;
        grid-row-gap: 30px;
        row-gap: 30px;
        justify-content: center;
        width: 100%;
        top: 15vh;
        position: absolute;
        background-color: white;
        height: auto;    
        border-bottom-left-radius: 20px;    
        border-bottom-right-radius: 20px;  
        color: black;
        display: flex;
        z-index: 1;
   }
   .navBarLinks{
    transition: max-height 0.15s ease-out;
    overflow: hidden;

   }
    nav #hidden{
        display: none;
    }
    .navBarLinks ul{
        list-style: none;
        padding: 0;
        margin-left: 15px;

    }
    .navBarLinks ul a{
        color: black;

    }
    .navBarLinks li{
        
        font-size:3vh;
        margin: 20px;
    }
    #subshow{

        flex-direction: column;
        grid-row-gap: 30px;
        row-gap: 30px;
        align-items: center;
        justify-content: center;
        width: 100%;
        z-index: 1;   

    }
    #subhidden{
        display: none;
    }
    .blurryBack{
        opacity: 0.5;
        height: 100%;
        width: 100%;
        background-color: black;
        position: fixed;
    }
}


footer{
    background-color: white;
    color: black;
    height: 22vh;
    border-radius: 20px 20px 0 0;
